import drawData from '../draw-data';
const publicStyle = {
  width: '30px',
  height: '30px',
  cursor: 'pointer',
  display: 'inline-block',
  backgroundSize: '25px',
  backgroundColor: '#fff',
  backgroundPosition: 'center',
  backgroundRepeat: 'no-repeat',
  borderRadius: '4px',
  verticalAlign: 'middle',
  margin: '2px'
}
const hover = (event) => {
  event.currentTarget.style.backgroundColor = 'rgba(14,19,24,.15)'
}


const setActive = (event) => {
  let active = +event.dataset.active;
  if (!active) {
    event.dataset.active = 1;
    event.style.backgroundColor = 'rgba(14,19,24,0.15)';
  } else {
    event.dataset.active = 0;
    event.style.backgroundColor = '#fff';
  }
}
export default function () {
  const {unit} = drawData.getParams();
 
  return [
    {
      title: "文字内容",
      name: 'text',
      type: 'textarea',
      on: {
        change:()=>{
          console.log("------")
        },
        input: function (event,e) {
          let activeDom = drawData.getActive();
          let textDom = activeDom.getElementsByTagName('span')[0]

          if(activeDom){
            textDom.innerText = e.value;
            activeDom.style.minWidth='';
            activeDom.style.minHeight=''
          }
        }
      }
    },
    {
      title: "字体",
      name: 'fontFamily',
     
      type: 'select',
      options: [{
        label: 'Apple',
        value: 'Apple'
      }, {
        label: 'Orange',
        value: 'Orange'
      }],
      on: {
        change: (evt) => {
          console.log('ppppp', evt.value)
        }
      }
    },
    {
      title: "字号",
      name: 'fontSize',
      type: 'select',
      isInput:true,
      options: [{
        label: '14',
        value: '14'
      }, {
        label: '16',
        value: '16'
      }],
      on: {
        change: (evt,e) => {
          console.log("------","kkkk")
          let activeDom = drawData.getActive();
          if(activeDom){
          activeDom.style.fontSize = e.value+unit;
          }
        }
      }
    },
    {
      title: "行高",
      name: 'lineHeight',
      isInput:true,
      type: 'select',
      options: [{
        label: '14',
        value: '14'
      }, {
        label: '16',
        value: '16'
      }],
      on: {
        change: (evt,e) => {
          let activeDom = drawData.getActive();
          if(activeDom){
          activeDom.style.lineHeight = e.value+unit;
        }
        }
      }
    },
    {
      title: "颜色",
      name: 'color',
      type: 'color',
      on: {
        change:(event,value)=>{
          // console.log(e.value,"jjjj")
          let activeDom = drawData.getActive();
          if(activeDom){
            activeDom.style.color =value;
          }
         
        }
      }

    },
    {
      title: "",
      name: 'textAlign',
      type: 'radio-button',
      style:{
        display:'inline-block'
      },
      on:{
        change:(event,dom)=>{
          // console.log("oooo",dom.value,event)
         let activeDom = drawData.getActive();
         activeDom.style.textAlign = dom.value;
        }
      },
      options: [{
        value:'left',
        label:'',
        url:'data:image/svg+xml;base64,'
      }, {
        value:'center',
        label:'',
        url:'data:image/svg+xml;base64,',
      },
      {
        value:'right',
        label:'',
        url:'data:image/svg+xml;base64,',
      }, {
       value:'justify',
       label:'',
       url:'data:image/svg+xml;base64,'

      }],


    },
    {
      title: "",
      name: 'fontWeight',
      type: 'switch',
      style:{
        padding:'0px',margin:'0px 5px'
      },
      url:'data:image/svg+xml;base64,',
      on: {
        change: function (event,e) {
          let activeDom = drawData.getActive();
          // console.log(event,e)
          if(e.checked){
            activeDom.style.fontWeight = 'bold';
            return ;
          }
         activeDom.style.fontWeight = 'normal';
        }
      }
    },
    {
      title: "",
      name: 'fontStyle',
      type: 'switch',
      style:{
        padding:'0px',margin:'0px'
      },
      url:'data:image/svg+xml;base64,',
      on: {
        change: function (event,e) {
          let activeDom = drawData.getActive();
          // console.log(event,e)
          if(e.checked){
            activeDom.style.fontStyle = 'italic';
            return ;
          }
         activeDom.style.fontStyle = 'normal';
        }
      }
    },
    {
      title: "",
      name: 'textDecoration',
      type: 'switch',
      style:{
        padding:'0px',margin:'5px'
      },
      url:'data:image/svg+xml;base64,',
      on: {
        change: function (event,e) {
          let activeDom = drawData.getActive();
          // console.log(event,e)
          if(e.checked){
            activeDom.style.textDecoration = 'underline';
            return ;
          }
         activeDom.style.textDecoration = 'none';
        }
      }
    },
  ]
}

